import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './assets/css/App.css';
import CurrencyInput from 'react-currency-masked-input';

function Modal() {
    const [showModal, setShowModal] = useState("none")
    const [users, setUsers] = useState([])
    const [showList, setShowList] = useState("block")

    useEffect(() => {
        axios.get(`https://www.mocky.io/v2/5d531c4f2e0000620081ddce`)
        .then(res => {
        const users = res.data;
        setUsers( users );
      })
        
    },[])
    
    const clickPay = (obj) => {
        setShowModal("block")
        setUsers(obj)
        setShowList(obj)        
    }

    return <>
        { users.map(user => 
            <div style={{display:showList}}>
                <div className="container">
                <img src={user.img} alt="Perfil"/>
                <div className="data" >
                    <p>{user.name}</p>
                    <p>ID: {user.id} - Nome de Usuário: {user.username}</p>
                </div>
                <div className="container-btn">
                  <button className="btn" onClick={() => { clickPay( users ) }} >Pagar</button>
                </div>
            </div>   
            </div>
        )}

        <div className="overlay" style={{display:showModal}}>
            <div className="container-payment">
                <div className="items-payment">
                    <h1 className="title-payment">Forma de Pagamento</h1>
                    <div className="sub-title-payment">
                        <div className="sub-title-payment1">
                            Pagamento Para:
                        </div>
                        <div className="sub-title-playment2">
                            {setUsers.name}
                        </div>
                    </div>
                    <form className="form-payment">
                        <CurrencyInput name="myInput" className="input-form-payment" type="number"pattern="\d*" placeholder="R$0,00" required />
                        <select className="select-form-payment" required>
                            <option></option>
                            <option>Cartão 1836</option>
                            <option>Cartão 5460</option>
                            <option>Cartão 4890</option>
                        </select>
                        <div className="buttons">
                            <button className="button-form-payment btnx">
                                Pagar
                            </button>
                            <button onClick={() => setShowList("block")} className="buttons btnx">
                                Voltar
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </>

}

export default Modal;